import React from 'react';
import { Drawer, List, ListItemButton, ListItemIcon, ListItemText, Toolbar, Box } from '@mui/material';
import { NavLink } from 'react-router-dom';
import DashboardIcon from '@mui/icons-material/Dashboard';
import InventoryIcon from '@mui/icons-material/Inventory';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import PeopleIcon from '@mui/icons-material/People';
import { useTranslation } from 'react-i18next';

const AdminSidebar = ({ drawerWidth }) => {
    const { t } = useTranslation();

    const menuItems = [
        { text: t('admin.sidebar.dashboard'), icon: <DashboardIcon />, path: '/admin/dashboard' },
        { text: t('admin.sidebar.products'), icon: <InventoryIcon />, path: '/admin/products' },
        { text: t('admin.sidebar.orders'), icon: <ShoppingCartIcon />, path: '/admin/orders' },
        { text: t('admin.sidebar.users'), icon: <PeopleIcon />, path: '/admin/users' },
    ];

    const navLinkStyle = {
        textDecoration: 'none', // Loi b gch chn ca link
        color: 'inherit',       // K tha mu ch
        display: 'block',       // m bo NavLink chim ton b chiu rng
    };

    const activeListItemStyle = {
        backgroundColor: 'primary.light', // Mu nn khi c chn
        color: 'primary.contrastText',    // Mu ch v icon khi c chn
        '& .MuiListItemIcon-root': {
            color: 'primary.contrastText', // m bo icon cng i mu
        },
        '&:hover': {
            backgroundColor: 'primary.main', // Hiu ng hover m hn mt cht
        },
        // Thm mt ng vin bn tri  nhn mnh
        borderLeft: 4,
        borderColor: 'primary.dark',
    };

    return (
        <Drawer
            variant="permanent"
            sx={{
                width: drawerWidth,
                pt:-8,
                flexShrink: 0,
                [`& .MuiDrawer-paper`]: { 
                    width: drawerWidth, 
                    boxSizing: 'border-box',
                    bgcolor: 'background.default', // Mu nn nh nhng hn
                },
            }}
        >
            <Toolbar />
            <Box sx={{ overflow: 'auto' }}>
                <List component="nav">
                    {menuItems.map((item) => (
                        <NavLink to={item.path} key={item.text} style={navLinkStyle} end>
                            {({ isActive }) => (
                                <ListItemButton sx={isActive ? activeListItemStyle : {}}>
                                    <ListItemIcon>{item.icon}</ListItemIcon>
                                    <ListItemText primary={item.text} />
                                </ListItemButton>
                            )}
                        </NavLink>
                    ))}
                </List>
            </Box>
        </Drawer>
    );
};

export default AdminSidebar;
