// MOCK IMPLEMENTATION

let mockNotifications = [
    { id: 1, message: "n hng #102 ca bn  c xc nhn.", read: false, createdAt: new Date(Date.now() - 1000 * 60 * 5).toISOString(), link: "/profile/orders" },
    { id: 2, message: "Cho mng bn n vi BookNest!", read: false, createdAt: new Date(Date.now() - 1000 * 60 * 60 * 24).toISOString(), link: "/" },
    { id: 3, message: "n hng #101  c giao thnh cng.", read: true, createdAt: new Date(Date.now() - 1000 * 60 * 60 * 48).toISOString(), link: "/profile/orders" },
];

const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

export const getNotificationsApi = async () => {
  await delay(400);
  console.log("MOCK: getNotificationsApi");
  return [...mockNotifications];
};

export const markAllReadApi = async () => {
  await delay(200);
  console.log("MOCK: markAllReadApi");
  mockNotifications.forEach(n => n.read = true);
  return { success: true };
};

export const sendEmailApi = async (payload) => {
  await delay(1000);
  console.log("MOCK: sendEmailApi", payload);
  // M phng gi email thnh cng
  return { success: true, message: "Email sent successfully." };
};